<script>
export default {
    data() {
        return {
            typedText: '',
            text: ['Hi!', 'Nice to meet u!'],
            index: 0,
            typingSpeed: 50,
            deletingSpeed: 20,
            showCaret: false,
            userName: ''
        }
    },
    mounted() {
        this.type();
    },
    methods: {
        type() {
            setTimeout(() => {
                if (this.index >= this.text.length) {
                    this.index = 0;
                }
                this.typedText = this.text[this.index].slice(0, this.typedText.length + 1);
                this.showCaret = false;
                if (this.typedText.length === this.text[this.index].length) {
                    setTimeout(() => {
                        this.delete();
                    }, 1000);
                } else {
                    this.showCaret = true;
                    this.type();
                }
            }, this.typingSpeed);
        },
        delete() {
            setTimeout(() => {
                this.showCaret = false;
                this.typedText = this.typedText.slice(0, this.typedText.length - 1);
                if (this.typedText.length === 0) {
                    this.index++;
                    this.type();
                } else {
                    this.delete();
                }
            }, this.deletingSpeed);
        },
    },
}
</script>


<template>
    <div class="greeting-container">
        <div class="text-container">
            <div class="text-message">
                <h1>&nbsp;{{ typedText }}<span class="caret" v-if="showCaret"></span></h1>
                <h1>&nbsp;I'm Yufan Zhang.</h1>
            </div>
            <div class="text-title">
                <h3>Data Science Undergrad @DukeKunshan</h3>
            </div>
            <div class="text-bio">
                <p>I am currently pursuing in a degree of B.S. in Data Science at Duke Kunshan
                    University.</p>
                <p>My professional interest lies on the intersection between Artificial Intelligence and Computational
